// Sinônimos
$green: mediumaquamarine;

// Definições
$color-accent: $green;
$header-height: 5rem;
$screen-widths: (
  xs: 0,
  sm: 500px,
  md: 800px,
  lg: 1200px,
);

@mixin screen-size($size: xs) {
  @media (min-width: map-get($screen-widths, $size)) {
    @content;
  }
}

:root {
  --color-accent: mediumaquamarine;
  --color-accent-darker: #{darken($color-accent, 4)};
  --color-accent-darkest: #{darken($color-accent, 16)};
  --menu-bg: #fff;
  --menu-color: #333;
}
@media (prefers-color-scheme: dark) {
  :root {
    --menu-bg: #333;
    --menu-color: #fff;
  }
}
@mixin unlink {
  color: inherit;
}
html {
  font: 16px/24px "Titillium Web", sans-serif;
  box-sizing: border-box;
}
* {
  box-sizing: inherit;
}
html,
body {
  height: 100%;
  margin: 0;
}
a {
  color: var(--color-accent-darkest);
  text-decoration: none;
}

// Logo
.logo {
  padding: 1rem 2rem;
  height: 100%;

  img {
    display: block;
    height: 100%;
    width: 100%;
    object-fit: contain;
    object-position: center;
  }
}

// Layout
.layout {
  min-height: 100%;
  width: 100%;
  display: grid;
  grid-template-areas:
    "logo header"
    "aside content"
    "footer footer";
  grid-template-rows: $header-height 1fr auto;
  grid-template-columns: 18rem 1fr;
}
.layout__logo {
  grid-area: logo;
  background: var(--color-accent);
}
.layout__header {
  grid-area: header;
  background: var(--color-accent-darker);
}
.layout__aside {
  grid-area: aside;
  background: #ddd;
}
.layout__content {
  grid-area: content;
  background: whitesmoke;
}
.layout__footer {
  grid-area: footer;
}

// Grid
.grid {
  margin: 0 auto;
  padding: 0 2rem;
}

// Filter
.filter {
  background-color: #ddd;
  padding: 2rem;
  margin: 2rem 0;
}

// Deck
.deck {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(15rem, 1fr));
  margin: 4rem 0;
  gap: 1rem;
}

.footer {
  font-size: 13px;
  padding: 4rem;
  background: #333;
  color: white;
  height: 400px;
  text-align: end;
}

// Shablau
.aside {
  position: relative;
  height: 100%;
  background: var(--menu-bg);
  color: var(--menu-color);
  display: flex;
  justify-content: space-between;
  flex-flow: column nowrap;
}

.profile {
  position: sticky;
  bottom: 0;
}
.profile__content {
  color: black;
  background: white;
  padding: 1rem;
}
.profile__content::before {
  content: "";
  display: block;
  width: 100%;
  height: 1rem;
  border-top: 1px solid #dcdcdc;
}
.profile__edit a {
  color: black;
  background: white;
  text-decoration: none;
  text-align: center;
  display: block;
  padding: 1rem;
}
.profile__edit a:hover {
  color: white;
  background-color: coral;
}
