:root {
  --card-bg: #fff;
  --card-color: #333;
  --card-border: #ddd;
  --card-title-color: #666;
}
@media (prefers-color-scheme: dark) {
  :root {
    --card-bg: #333;
    --card-color: #fff;
    --card-border: #111;
    --card-title-color: #aaa;
  }
}

.card {
  height: 100%;
  display: flex;
  flex-flow: column nowrap;
  background-color: var(--card-bg);
  color: var(--card-color);
  border: 1px solid var(--card-border);
}
.card__header {
  padding: 0.5rem;
}
.card__title {
  font-weight: bolder;
  color: var(--card-title-color);
}
.card__content {
  flex: 1 0;
}
.card__footer {
  justify-self: flex-end;
}
.card__image img {
  width: 100%;
  height: auto;
  display: block;
}
.card__text {
  padding: 0 1rem;
}
.card__controls {
  font-size: 85%;
  padding: 0.5rem 1rem;
  display: flex;
  flex-flow: row-reverse wrap;
}
